<template style="padding: 0px 0px;">
  <d2-container style="padding: 0px 0px;">
    <Panel class="panel-body panel-body-noheader">
      <LinkButton iconCls="icon-add" :plain="false" @click="addServicetype">添加</LinkButton>
    </Panel>

    <!-- 服务表格 -->
    <div style="flex: 1;">
        <DataGrid
                :pagination="true"
                :total="total"
                :pageSize="pageSize"
                :pagePosition="pagePosition"
                :data="serviceTable"
                style="height: 100%;">
            <GridColumn align="center" cellCss="datagrid-td-rownumber" width="30">
                <template slot="body" slot-scope="scope">
                    {{scope.rowIndex + 1}}
                </template>
            </GridColumn>

            <GridColumn title="服务名称" @click="callServiceInfo(scope.row)">  <!--  field="service_name" -->
                <template slot="body" slot-scope="scope">
                    <u style="cursor: pointer" @click="callServiceInfo(scope.row)">{{scope.row.servicename}}</u>
                </template>
            </GridColumn>
            <GridColumn field="servicecompany" width="200px" title="参评企业"></GridColumn>
            <GridColumn field="servicetype" title="服务安全特征"></GridColumn>
            <GridColumn field="testorganization" title="测评机构"></GridColumn>
            <GridColumn field="servicelevel" title="测评等级" width="120px">
                <template slot="header">
                    <div class="item">
                        <div class="title">测评等级</div>
                    </div>
                </template>
                <template slot="body" slot-scope="scope">
                    <p v-if="scope.row.servicelevel === null">
                        <!--尚未测评-->
                        <LinkButton iconCls="icon-add"  :toggle="false" @click="addEvaluation(scope.row)">测评</LinkButton>
                    </p>
                    <p v-else>{{scope.row.servicelevel}}</p>
                </template>
            </GridColumn>
            <GridColumn title="测评结果" width="140px">
                <template slot="header">
                    <div class="item">
                        <div class="title">测评结果</div>
                    </div>
                </template>
                <template slot="body" slot-scope="scope">
                    <p v-if="scope.row.servicelevel === null">
                        <!--尚未测评-->
                    </p>
                    <p v-else>
                        <LinkButton iconCls="icon-search" :toggle="false" @click="callEvaluation(scope.row)">
                            查看
                        </LinkButton>

                        <LinkButton iconCls="icon-edit" :toggle="false" @click="updateEvaluation(scope.row)">
                            更新
                        </LinkButton>
                    </p>

                    <!--<LinkButton iconCls="icon-save" :toggle="true" @click="callEvaluation(scope.row)">导出</LinkButton>-->
                </template>
            </GridColumn>

            <GridColumn title="定级结果" width="120px">
                <template slot="header">
                    <div class="item">
                        <div class="title">定级结果(指标项)</div>
                    </div>
                </template>
                <template slot="body" slot-scope="scope">
                    <p v-if="scope.row.servicelevel === null">
                        <!--尚未测评-->
                    </p>
                    <p v-else>
                        <LinkButton iconCls="icon-search" :toggle="false" @click="callClassification_target(scope.row)">
                            查看
                        </LinkButton>
                    </p>

                    <!--<LinkButton iconCls="icon-save" :toggle="true" @click="exportExcel()">导出</LinkButton>-->
                </template>
            </GridColumn>

            <GridColumn title="定级结果" width="120px">
                <template slot="header">
                    <div class="item">
                        <div class="title">定级结果(安全族)</div>
                    </div>
                </template>
                <template slot="body" slot-scope="scope">
                    <p v-if="scope.row.servicelevel === null">
                        <!--尚未测评-->
                    </p>
                    <p v-else>
                        <LinkButton iconCls="icon-search" :toggle="false" @click="callClassification_family(scope.row)">
                            查看
                        </LinkButton>
                    </p>
                    <!--<LinkButton iconCls="icon-save" :toggle="true" @click="callClassification_family(scope.row)">导出</LinkButton>-->
                </template>
            </GridColumn>

            <GridColumn title="定级结果" width="120px">
                <template slot="header">
                    <div class="item">
                        <div class="title">定级结果(SAG)</div>
                    </div>
                </template>
                <template slot="body" slot-scope="scope">
                    <p v-if="scope.row.servicelevel === null">
                        <!--尚未测评-->
                    </p>
                    <p v-else>
                        <LinkButton iconCls="icon-search" :toggle="false" @click="callClassification_sag(scope.row)">
                            查看
                        </LinkButton>
                    </p>
                    <!--<LinkButton iconCls="icon-save" :toggle="true" @click="callClassification_sag(scope.row)">导出</LinkButton>-->
                </template>
            </GridColumn>
            <GridColumn title="操作" width="80px">
                <template slot="header">
                    <div class="item">
                        <div class="title">操作</div>
                    </div>
                </template>
                <template slot="body" slot-scope="scope">
                    <LinkButton iconCls="icon-remove" :toggle="true" @click="callDelete(scope.row)">删除</LinkButton>
                </template>
            </GridColumn>
        </DataGrid>
    </div>
    <!-- 服务表格 -->

    <!-- 服务信息弹窗 -->
    <el-dialog title="服务信息" width="80%" :visible.sync="dialogServiceInfoVisiable" v-if="dialogServiceInfoVisiable">

      <table :data="formServiceInfo" border="1" cellspacing="0" cellpadding="" width="100%">
        <tr>
          <td align="center" valign="middle" width="70" height="40">服务名称</td>
          <td width="70" align="center" valign="middle">{{formServiceInfo.servicename}}</td>
          <td align="center" valign="middle" width="70">云服务类型</td>
          <td width="70" align="center" valign="middle">{{formServiceInfo.servicetype}}</td>
          <td align="center" valign="middle" width="70">服务对象</td>
          <td width="70" align="center" valign="middle">{{formServiceInfo.serviceobject}}</td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="40">服务地点</td>
          <td align="center" valign="middle">{{formServiceInfo.servicelocation}}</td>
          <td align="center" valign="middle">服务期限</td>
          <td align="center" valign="middle">{{formServiceInfo.serviceterm}}</td>
          <td colspan="2"></td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="80">服务介绍</td>
          <td colspan="5" style="text-indent:2em;">{{formServiceInfo.serviceintro}}</td>
        </tr>

        <tr>
          <td align="center" valign="middle" height="40" colspan="6">公司介绍</td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="40">公司名称</td>
          <td align="center" valign="middle">{{formServiceInfo.companyname}}</td>
          <td align="center" valign="middle">法人代表</td>
          <td align="center" valign="middle">{{formServiceInfo.legalperson}}</td>
          <td align="center" valign="middle">社会统一代码</td>
          <td align="center" valign="middle">{{formServiceInfo.suic}}</td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="40">注册日期</td>
          <td  align="center" valign="middle">{{formServiceInfo.regdate}}</td>
          <td align="center" valign="middle">注册资本(万元)</td>
          <td  align="center" valign="middle">{{formServiceInfo.regcapital}}</td>
          <td align="center" valign="middle">企业类型</td>
          <td  align="center" valign="middle">{{formServiceInfo.companytype}}</td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="40">公司地址</td>
          <td colspan="5">{{formServiceInfo.regaddress}}</td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="80">公司介绍</td>
          <td colspan="5" style="text-indent:2em;">{{formServiceInfo.businessscope}}</td>
        </tr>

        <tr style="border: 2; cellpadding: 0;">
          <td align="center" valign="middle" height="40" colspan="6">测评入口</td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="40">服务地址</td>
          <td>{{formServiceInfo.serviceurl}}</td>
          <td align="center" valign="middle">测试账号</td>
          <td>{{formServiceInfo.username}}</td>
          <td align="center" valign="middle">测试密码</td>
          <td>{{formServiceInfo.password}}</td>
        </tr>
        <tr>
          <td align="center" valign="middle" height="40">服务IP</td>
          <td>{{formServiceInfo.ip}}</td>
          <td align="center" valign="middle">服务端口</td>
          <td>{{formServiceInfo.port}}</td>
          <td align="center" valign="middle">服务协议</td>
          <td>{{formServiceInfo.protocol}}</td>
        </tr>
      </table>
    </el-dialog>
    <!-- 服务信息弹窗 -->

    <!-- 添加服务类型弹窗 -->
    <el-dialog title="选择服务类型" style="height: 80%" width="80%" :visible.sync="dialogServiceChoose" v-if="dialogServiceChoose">
        <table :data="formServiceInfo" border="1" cellspacing="0" cellpadding="" width="100%">
            <tr>
                <td align="center" valign="middle" width="70" height="40">
                    <!--// // 数据服务安全-->
                    <!--// "数据存储下载",-->
                    <!--// "数据分析治理",-->
                    <!--// // 计算服务安全-->
                    <!--// "虚拟机、虚拟桌面",-->
                    <!--// "服务器计算资源管理",-->
                    <!--// "开发运维平台",-->
                    <!--// // 社交通讯服务安全-->
                    <!--// "即时通信",-->
                    <!--// "邮件",-->
                    <!--// // 交易支付服务安全-->
                    <!--// "交易过程服务",-->
                    <!--// "在线支付",-->
                    <!--// // 多媒体服务安全-->
                    <!--// "流媒体信息交互与处理",-->
                    <!--// "流媒体审核治理",-->
                    <!--// // 网络服务安全-->
                    <!--// "网络安全协议服务",-->
                    <!--// "流量管理",-->
                    <!--// "网络资源管理",-->
                    <!--// // 信息检索服务安全-->
                    <!--// "内容、知识检索的信息服务",-->
                    <!--// // 信息管理服务安全-->
                    <!--// "个人和组织信息管理",-->
                    <!--// "社会事务信息管理",-->
                    <!--// // 信息发布订阅服务安全-->
                    <!--// "信息订阅"-->
                    <!--// "信息发布"-->
                    <el-checkbox-group v-model="servicetypecontent">
                        <!-- 数据生命周期服务 -->
                        <el-checkbox label="数据存储下载"></el-checkbox>
                        <el-checkbox label="数据分析治理"></el-checkbox>
                        <!-- 计算服务安全 -->
                        <el-checkbox label="虚拟机、虚拟桌面"></el-checkbox>
                        <el-checkbox label="服务器计算资源管理"></el-checkbox>
                        <el-checkbox label="开发运维平台"></el-checkbox>
                        <!-- 社交通讯服务安全 -->
                        <el-checkbox label="即时通信"></el-checkbox>
                        <el-checkbox label="邮件"></el-checkbox>
                        <!-- 交易支付服务安全 -->
                        <el-checkbox label="交易过程服务"></el-checkbox>
                        <el-checkbox label="在线支付"></el-checkbox>
                        <!-- 多媒体服务安全 -->
                        <el-checkbox label="流媒体信息交互与处理"></el-checkbox>
                        <el-checkbox label="流媒体审核治理"></el-checkbox>
                        <!-- 网络服务安全 -->
                        <el-checkbox label="网络安全协议服务"></el-checkbox>
                        <el-checkbox label="流量管理"></el-checkbox>
                        <el-checkbox label="网络资源管理"></el-checkbox>
                        <!-- 信息检索服务安全 -->
                        <el-checkbox label="内容、知识检索的信息服务"></el-checkbox>
                        <!-- 信息管理服务安全 -->
                        <el-checkbox label="个人和组织信息管理"></el-checkbox>
                        <el-checkbox label="社会事务信息管理"></el-checkbox>
                        <!-- 信息发布订阅服务安全 -->
                        <el-checkbox label="信息订阅"></el-checkbox>
                        <el-checkbox label="信息发布"></el-checkbox>
                    </el-checkbox-group>
                </td>
            </tr>
            <!--<tr>-->
                <!--<td align="center" valign="middle" width="70" height="50">-->
                    <!--<el-button type="primary" round @click="checkServiceType()">判断服务类型</el-button>-->
                <!--</td>-->
            <!--</tr>-->
            <!--<tr>-->
                <!--<td align="center" valign="middle" width="70" height="40">-->
                    <!--{{formServiceInfo.servicetype}}-->
                <!--</td>-->
            <!--</tr>-->
        </table>


      <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="submitServiceType()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 添加服务类型弹窗 -->

    <!-- 添加服务信息弹窗 -->
    <el-dialog title="添加服务信息" width="80%" :visible.sync="dialogServiceInfoAddVisiable" v-if="dialogServiceInfoAddVisiable">
      <el-form ref="formServiceInfo" @submit.native.prevent label-width="240px"  :label-position="service_info_labelPosition">
        <table border="1" cellspacing="0" cellpadding="" width="100%">
          <tr>
            <td align="center" valign="middle" width="70" height="40">服务名称</td>
            <td width="70" align="center" valign="middle">
              <TextBox required="required" placeholder="服务名称" v-model="formServiceInfo.servicename" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle" width="70">服务类型</td>
            <td width="70" align="center" valign="middle">
              <!--<TextBox placeholder="服务类型" v-model="formServiceInfo.servicetype" style="width: 100%;"></TextBox>-->
              <ComboBox placeholder="服务类型" v-model="formServiceInfo.servicetype" :data="servicetypeword" style="width: 100%;"></ComboBox>
            </td>
            <td align="center" valign="middle" width="70">测试机构</td>
            <td width="70" align="center" valign="middle">
              <TextBox placeholder="参评机构" v-model="formServiceInfo.testorganization" style="width: 100%;"></TextBox>
            </td>
          </tr>
          <tr>
            <td align="center" valign="middle" width="70" height="40">云服务类型</td>
            <td width="70" align="center" valign="middle">
              <TextBox placeholder="云服务类型" v-model="formServiceInfo.cloudservicetype" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle" width="70">服务对象</td>
            <td width="70" align="center" valign="middle">
              <TextBox placeholder="服务对象" v-model="formServiceInfo.serviceobject" style="width: 100%;"></TextBox>
            </td>
            <td colspan="2"></td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="40">服务地点</td>
            <td align="center" valign="middle">
              <TextBox placeholder="服务地点" v-model="formServiceInfo.servicelocation" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">服务期限</td>
            <td align="center" valign="middle">
              <TextBox placeholder="服务期限" v-model="formServiceInfo.serviceterm" style="width: 100%;"></TextBox>
            </td>
            <td colspan="2"></td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="80">服务介绍</td>
            <td colspan="5">
              <TextBox :multiline="true" placeholder="服务介绍" v-model="formServiceInfo.serviceintro"
                       style="width: 100%;height: 100px;"></TextBox>
            </td>
          </tr>

          <tr>
            <td align="center" valign="middle" height="40" colspan="6">公司介绍</td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="40">参评公司名称</td>
            <td align="center" valign="middle">
              <TextBox placeholder="公司名称" v-model="formServiceInfo.companyname" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">法人代表</td>
            <td align="center" valign="middle">
              <TextBox placeholder="法人代表" v-model="formServiceInfo.legalperson" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">社会统一代码</td>
            <td align="center" valign="middle">
              <TextBox placeholder="社会统一代码" v-model="formServiceInfo.suic" style="width: 100%;"></TextBox>
            </td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="40">注册日期</td>
            <td  align="center" valign="middle">
              <TextBox placeholder="参考格式范例：1990/01/01" v-model="formServiceInfo.regdate" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">注册资本</td>
            <td  align="center" valign="middle">
              <TextBox placeholder="注册资本(单位：万元)" v-model="formServiceInfo.regcapital" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">企业类型</td>
            <td  align="center" valign="middle">
              <TextBox placeholder="企业类型" v-model="formServiceInfo.companytype" style="width: 100%;"></TextBox>
            </td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="40">公司地址</td>
            <td colspan="5">
              <TextBox placeholder="公司地址" v-model="formServiceInfo.regaddress" style="width: 100%;"></TextBox>
            </td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="80">公司介绍</td>
            <td colspan="5">
              <TextBox placeholder="公司介绍" v-model="formServiceInfo.businessscope" style="width: 100%;height: 100px;"></TextBox>
            </td>
          </tr>

          <tr style="border: 2; cellpadding: 0;">
            <td align="center" valign="middle" height="40" colspan="6">测评入口</td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="40">服务地址</td>
            <td>
              <TextBox placeholder="服务地址" v-model="formServiceInfo.serviceurl" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">测试账号</td>
            <td>
              <TextBox placeholder="测试账号" v-model="formServiceInfo.username" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">测试密码</td>
            <td>
              <TextBox placeholder="测试密码" v-model="formServiceInfo.password" style="width: 100%;"></TextBox>
            </td>
          </tr>
          <tr>
            <td align="center" valign="middle" height="40">服务IP</td>
            <td>
              <TextBox placeholder="服务IP" v-model="formServiceInfo.ip" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">服务端口</td>
            <td>
              <TextBox placeholder="服务端口" v-model="formServiceInfo.port" style="width: 100%;"></TextBox>
            </td>
            <td align="center" valign="middle">服务协议</td>
            <td>
              <TextBox placeholder="服务协议" v-model="formServiceInfo.protocol" style="width: 100%;"></TextBox>
            </td>
          </tr>
        </table>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitService()">确 定</el-button>
      </div>


    </el-dialog>
    <!-- 添加服务信息弹窗 -->

    <!-- 测评结果弹窗 -->
    <el-dialog title="测评结果" width="80%" style="height: 80%" :visible.sync="dialogEvaluationVisiable">
      <el-table id="formEvaluation" height="400" :data="formEvaluation" border style="height: 100%;width: 100%"
                :span-method="spanMethod">
        <el-table-column prop="securitygroup" label="安全族">
        </el-table-column>
        <el-table-column prop="indexname" label="指标项">
        </el-table-column>
        <el-table-column prop="indexdescription" label="指标释义">
        </el-table-column>
        <el-table-column label="等级">
          <template  slot-scope="scope">
            <span v-if="scope.row.target_level == 1">基础定义级</span>
            <span v-if="scope.row.target_level == 2">增强控制级</span>
            <span v-if="scope.row.target_level == 3">持续优化级</span>
            <span v-if="scope.row.target_level == 4">量化完善级</span>
          </template>
        </el-table-column>
        <el-table-column prop="level_score" label="符合度">
        </el-table-column>
        <!--<el-table-column prop="index_score" label="定级">-->
        <!--</el-table-column>-->
      </el-table>
    </el-dialog>
    <!-- 测评结果弹窗 -->

    <!-- 添加测评弹窗 -->
    <el-dialog title="添加测评内容" width="80%" :visible.sync="dialogEvaluationAddVisiable">
      <el-table id="formEvaluationAdd" height="400" :data="formEvaluationAdd" border style="height: 100%;width: 100%"
                :span-method="spanMethod_formEvaluationAdd">
          <el-table-column prop="securitygroup" label="安全族">
          </el-table-column>
          <el-table-column prop="indexname" label="指标项">
          </el-table-column>
          <el-table-column prop="indexdescription" label="指标释义">
          </el-table-column>
          <el-table-column label="等级">
              <template  slot-scope="scope">
                  <span v-if="scope.row.target_level == 1">基础定义级</span>
                  <span v-if="scope.row.target_level == 2">增强控制级</span>
                  <span v-if="scope.row.target_level == 3">持续优化级</span>
                  <span v-if="scope.row.target_level == 4">量化完善级</span>
              </template>
          </el-table-column>
          <el-table-column label="通过指标数">
              <template slot-scope="scope">
                <NumberBox v-model.number=scope.row.numerator :min=0 :spinners="false"
                           style="width: 40px;"></NumberBox>
              </template>
          </el-table-column>
          <el-table-column label="总指标数">
              <template slot-scope="scope">
                <NumberBox v-model.number=scope.row.denominator :min.number=scope.row.target_level
                           :spinners="false" style="width: 40px;"></NumberBox>
              </template>
          </el-table-column>
      </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogEvaluationAddVisiable = false">取 消</el-button>
          <el-button type="primary" @click="submitEvaluation()">确 定</el-button>
        </span>
    </el-dialog>
    <!-- 添加测评弹窗 -->

    <!-- 修改测评弹窗 -->
    <el-dialog title="修改测评内容" width="80%" :visible.sync="dialogEvaluationUpdateVisiable">
      <el-table id="formEvaluationUpdate" height="400" :data="formEvaluationUpdate" border style="height: 100%;width: 100%"
                :span-method="spanMethod_formEvaluationUpdate">
          <el-table-column prop="securitygroup" label="安全族">
          </el-table-column>
          <el-table-column prop="indexname" label="指标项">
          </el-table-column>
          <el-table-column prop="indexdescription" label="指标释义">
          </el-table-column>
          <el-table-column label="等级">
              <template  slot-scope="scope">
                  <span v-if="scope.row.target_level == 1">基础定义级</span>
                  <span v-if="scope.row.target_level == 2">增强控制级</span>
                  <span v-if="scope.row.target_level == 3">持续优化级</span>
                  <span v-if="scope.row.target_level == 4">量化完善级</span>
              </template>
          </el-table-column>
          <el-table-column label="通过指标数">
              <template slot-scope="scope">
                  <NumberBox v-model.number=scope.row.numerator :min=0 :spinners="false" style="width: 40px;"></NumberBox>
              </template>
          </el-table-column>
          <el-table-column label="总指标数">
              <template slot-scope="scope">
